.productInfo {
    display: flex;
    
    .rating{
        border-right: groove thin rgb(139, 131, 131) ;
        background-color: rgb(25, 25, 25);
        padding: 30px;
        width: 20%;
        min-width: 150px;
        text-align: left;
        color: white;

        h1{
            margin-top: 60px;
            text-align: center;
            font-size: 25px;
        }
        .rate{  
            text-align: center;
            color: rgb(244, 224, 5);
            padding: 20px;
            font-size: 30px;
            
        }
        .vote{  
            text-align: center;
            color: rgb(47, 63, 206);
            padding: 20px;
            font-size: 25px;
        }
        @include tablet{
            width: 30%;
        }
        @include mobile{
            padding: 0px;
            width: 100%;
        }
        
    }


    .info{
        border-right: groove thin rgb(139, 131, 131) ;
        background-color: rgb(25, 25, 25);
        width: 50%;
        padding: 20px;
        
        .heading{
            padding: 10px;
            position: relative;
            width: 100%;
            height: 50px;
            text-align: center;
            color: white;

            li{    
                font-size: 20px;
                display: inline-block;
                margin:20px;
                @include mobile{
                    padding: 10px;
                }
            }

            li:hover{    
                color: red;
                cursor: pointer;
            }
            @include mobile{  
                margin-bottom: 50px ;
            }
        }

        .bodying{
            text-align: left;
            color: white;
            font-size: 12px;

            p{  
                color: rgb(169, 166, 166);
                padding-top: 10px;
                font-size: 20px;
            }
        }
        @include tablet{
            width: 60%;
        }
        @include mobile{
            width: 100%;
            margin-bottom: 30px;
        }
        
    } 
    @include mobile{
        flex-direction: column;
    }
    
}
.media{
    z-index: 100;
    color: white;
    text-align: center;
    h4{
        margin-left: 20px;
        font-size: 30px;
        text-align: left;
    }
    iframe{
        margin: 26px;
        margin-bottom:40px ;
        @include tablet{
            margin-right: 20px;
            width: 200px;
            height: 150px;
        } 
        @include mobile{
            margin-right: 20px;
            width: 300px;
            height: 200px;
        }
        
    }
}
